@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff") format("woff");
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff") format("woff");
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff") format("woff");
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
:root {
  --title-font: "Oswald", "Arial", sans-serif;
  --description-font: "Lato", "Tahoma", sans-serif;
  --primary-color: #000000;
  --secondary-color: #111111;
  --description-color: #444444;
  --title-color: #222222;
  --pseudo-color: #d9d9d9;
  --pseudo-secondary-color: #ebebeb;
  --pseudo-third-color: #cdcdcd;
  --input-color: #e7e7e7;
  --background-color-primary: #ffffff;
  --background-color-secondary: #f2f2f2;
  --background-color-button: #68b738;
  --pseudo-fourth-color: #e6e6e6;
}

.page {
  background-color: #e5e5e5;
}

.page__body {
  color: var(--primary-color);
  margin: 0 auto;
