.main-nav--nojs .main-nav__container {
  position: static;
  min-height: 0;
}

.main-nav--nojs .main-nav__item {
  display: block;
}

.main-nav__item a {
  display: inline-block;
  text-decoration: none;
}

.main-nav__button-toggle {
  width: 60px;
  height: 50px;
  position: absolute;
  right: 1px;
  top: -52px;
  z-index: 4;
  cursor: pointer;
  border: none;
  background-color: var(--background-color-primary);
}
@media (min-width: 768px) {
  .main-nav__button-toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__button-toggle::before {
  position: absolute;
  content: "";
  background-color: var(--primary-color);
  width: 24px;
  height: 1px;
  left: 20px;
  top: 18px;
  z-index: 5;
  box-shadow: 0 8px 0 0 var(--primary-color), 0 16px 0 0 var(--primary-color);
}

.main-nav--opened .main-nav__button-toggle::after,
.main-nav--opened .main-nav__button-toggle::before,
.main-nav__button-toggle .main-nav__button-toggle::after,
.main-nav__button-toggle .main-nav__button-toggle::before {
  position: absolute;
  content: "";
  z-index: 5;
  background-color: var(--primary-color);
  width: 24px;
  height: 1px;
  left: 20px;
  top: 25px;
}
.main-nav--opened .main-nav__button-toggle::after,
.main-nav__button-toggle .main-nav__button-toggle::after {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__button-toggle::before,
.main-nav__button-toggle .main-nav__button-toggle::before {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: flex;
  }
}

